.nav__contents {
  display: flex;
  justify-content: space-between;
}

.nav__logo{
  position: fixed;
  top: 10px;
  left: 0;
  width: 80px;
  
  object-fit: contain;
  padding-left: 20px;
  cursor: pointer;
}
.nav {
  position: fixed;
  top: 0;
  padding: 10px;
  width: 100%;
  height: 30px;
  z-index: 1;
  /* Animation */
  transition-timing-function: ease-in;
  transition: all 0.5s;
}
.nav-left{
  display: flex;
  position: fixed;
  top: 5px;
  left: 150px;
  width: 400px;
  justify-content: space-around;
}
.nav__black {
  background-color: #111;

}
.nav__avatar {
  position: fixed;
  right: 20px;
  cursor: pointer;
  object-fit: contain;
  height: 30px;
}
.active-menu{
  color: #000;
  background-color: aliceblue;
  transition: all 0.3s;
  text-decoration: none;  
  cursor: pointer;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 1.8rem;
  padding-right: 1.8rem;
  margin-right: 1rem;
  padding-top: 0.3rem;
  padding-bottom: 0.3rem;
}

.active-menu-items{
  text-decoration: none;  
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 1.8rem;
  padding-right: 1.8rem;
  margin-right: 1rem;
  padding-top: 0.3rem;
  background-color: rgba(51,51,51,0.5);
  padding-bottom: 0.3rem;
}
.active-menu-items:hover{
  color: #000;
  background-color: aliceblue;
  transition: all 0.3s;
}
@media (max-width: 390px) {
  .nav__logo {
  width: 15%;
  }
  .active-menu-items{
    width: 20%;
    padding-left: 0;
    padding-right: 0;
    background-color: #111;
  }
  .active-menu-items h2{
    font-size: 1rem;
    
  }
  .nav-left{
    position:absolute;
    left: 30px;
    justify-content: center;
    background-color: #111;
  }
  
}