.banner{
    height: 448px;
    position: relative;
    color: white;
    object-fit: contain;
    margin-bottom: 30px;
}

.banner__contents{
    margin-left:  30px;
    padding-top:  140px;
    height: 190px;
}

.banner__title {
    font-size: 2.8rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner__description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    max-width: 360px;
    height: 80px;
}
.banner__fadeBottom{
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37,0.61),
        #111
    );
}